.modal__backdrop {
  position: fixed;
  background-color: #f5f5f5;
  opacity: 0.2;
  inset: 0 0 0 0;
  cursor: pointer;
  transition: 3s;
}

.modal__box {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: white;
  border: 2px solid black;
  border-radius: 10px;
  padding: 10px;
  box-shadow: 5px 5px 1px black;
  width: 20vw;
  bottom: 50%;
  left: 40vw;
  position: fixed;
}

.modal__box > p {
  font-size: 12px;
  font-weight: 400;
  padding-bottom: 10px;
}

.modal__box > button {
  background-color: black;
  color: white;
  border-radius: 10px;
  box-shadow: 5px 5px 1px gray;
  cursor: pointer;
}

@media only screen and (max-width: 600px) {
  .modal__box {
    width: 60vw;
    left: 20vw;
  }
}
